import React, {FunctionComponent, useEffect, useState} from 'react';
import ClassNames from './Image.module.scss';
import classNames from 'classnames';

interface Props {
    className?: string;
    data: any;
}

const Image: FunctionComponent<Props> = ({className, data}) => {
    console.log(data);
    const [loaded, setLoaded] = useState(false);
    const [imgSrc, setImgSrc] = useState(undefined);
    useEffect(() => {
        setImgSrc(data.fields.image.fields.file.url);
    }, []);
    return (
        <div className={classNames(ClassNames.Image, className, loaded && ClassNames.loaded)}>
            <img onLoad={() => setLoaded(true)} className={ClassNames.img} src={imgSrc} />
        </div>
    );
};

export default Image;
