import React, {FunctionComponent} from 'react';

import ClassNames from './SplitPage.module.scss';
import PageClassNames from '../../../../styles/page.module.scss';
import Navigation from "../../../Navigation/Navigation";
import {CmsPageProps} from "../../../../types/CmsPageProps";
import SplitContainer from "../../SplitContainer/SplitContainer";

interface Props {
    pageContext: {
        fields: {
            area1: any[];
            area2: any[];
        }
    }
}

const SplitPage: FunctionComponent<Props & CmsPageProps> =
    ({pageContext: {fields, title}}) => {
        const {area1, area2} = fields;
        return (
            <div className={PageClassNames.Page}>
                <div className={ClassNames.SplitPage}>
                    <Navigation/>
                    <SplitContainer area1={area1} area2={area2}/>
                </div>
            </div>
        );
    };

export default SplitPage;
